@import url('https://fonts.googleapis.com/css2?family=Inter+Tight:wght@400;500&family=Nunito:wght@300&family=Poppins:wght@300;400&family=Qwitcher+Grypen:wght@700&family=Rubik&display=swap');

.card-body {
  overflow: auto;
}

body {
  background-color: #15202B !important;
  font-family: 'Poppins', sans-serif;
}

.navbar {
  background-color: #1f3042 !important;
  z-index: 2;
}


.nav-link:hover {
  text-decoration: underline;

}

a {
  color: white !important;
}

.card {
  border-radius: 12px;
}

.card-body {
  background-color: black !important;
  color: white;
}

.card-body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 12px;

  background-color: #fbf7f7;
}

.card-body::-webkit-scrollbar {
  width: 10px;
  background-color: #0a0a0a;
}

.card-body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #2946d6;
}

.card-img-top {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.marquee {
  width: 100%;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee p {
  display: inline-block;
  padding-left: 100%;
  animation: marquee 15s linear infinite;
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}